import React from "react";

import { makeStyles } from "@material-ui/core/styles";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";

import styles from "assets/jss/material-kit-react/views/landingPageSections/nosotrosStyle.js";
const useStyles = makeStyles(styles);

export default function Relatos(props){
	const classes = useStyles();
	const {contenido} = props;

	const content = (d,i) => {
			if(i%2===0){
				return(
					<React.Fragment>
				        <GridItem xs={12} sm={12} md={6}>
				        	<img src={d.img} alt="" className={classes.muestras}/>
				        </GridItem>
				        <GridItem xs={12} sm={12} md={6}>
							{d.descripcion}
				        </GridItem>
					</React.Fragment>
				)
			}else{
				return(
					<React.Fragment>
				        <GridItem xs={12} sm={12} md={6}>
							{d.descripcion}
				        </GridItem>					
				        <GridItem xs={12} sm={12} md={6}>
				        	<img src={d.img} alt="" className={classes.muestras}/>
				        </GridItem>
					</React.Fragment>
				)				
			}
	}
  	const items = () => {
  		let it = contenido.map((d,i) => {
  				console.log(classes.relatos)
  				return(
			      <GridContainer className={classes.relatos} key={"relato_"+i}>
			      	{content(d,i)}
			      </GridContainer>
  				)
  		})
  		return it;
  	}
	return(
			    <div className={classes.section}>
			    	{items()}
			    </div>
	)
}
